.location-name{
    font-size:30px;
}


/* //Snow, Atmosphere, Clear, Clouds, Rain  all for day and night */ 

.clear.day{
    background: -webkit-linear-gradient(top right, #ff3300 0%, #ff9933 100%);
    background: linear-gradient(to bottom left, #ff3300 0%, #ff9933 100%);
  }
  
  .clear.night{
    background: -webkit-linear-gradient(bottom, #000066 0%, #660066 100%);
    background: linear-gradient(to top, #000066 0%, #660066 100%);
  }
  
  .clouds.day{
    background: -webkit-linear-gradient(bottom, #ff9900 0%, #ffffff 100%);
    background: linear-gradient(to top, #ff9900 0%, #ffffff 100%);
  }
  .clouds.night{
    background: -webkit-linear-gradient(top, #ffffff 0%, #000066 100%);
    background: linear-gradient(to bottom, #ffffff 0%, #000066 100%);
  }
  
  .snow.night{
    background: -webkit-linear-gradient(top left, #ccffff 0%, #9999ff 100%);
    background: linear-gradient(to bottom right, #ccffff 0%, #9999ff 100%);
  }
  .snow.day{
    background: -webkit-linear-gradient(top left, #ccffff 0%, #9999ff 100%);
    background: linear-gradient(to bottom right, #ccffff 0%, #9999ff 100%);
  }
  .rain.day{
    background: -webkit-linear-gradient(top right, #66ccff 0%, #3366cc 100%);
    background: linear-gradient(to bottom left, #66ccff 0%, #3366cc 100%);
  }
  .rain.night{
    background: -webkit-linear-gradient(top right, #66ccff 0%, #3366cc 100%);
    background: linear-gradient(to bottom left, #66ccff 0%, #3366cc 100%);
  }
  
  
